body{
    font-family: 'Nunito', sans-serif;
    padding: 0;
    margin: 0;
    overflow-x: hidden;
    color: #3E3F43;
}
a{
    color: #3E3F43;
    text-decoration: none;
}
// Шапка
.body__header{
    padding-top: 20px;
    padding-bottom: 140px;
}
.header{
    .header__wrapper{
        padding: 0 28px 0 80px;
    }
    .header__list{
        margin-bottom: 0;
        align-items: center;
    }
    .header__item{
        list-style: none;
    }
    .header__link{
        list-style: none;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
        color: rgba(34, 34, 34, 0.75);
    }
    .header__item--active{
        font-weight: 800;
        color: #DAA520;
    }
    .header__link-sing-up{
        text-transform: uppercase;
        font-weight: bold;
        padding: 9px 35px 9px 36px;
        border: 1px solid #3E3F43;
        box-sizing: border-box;
        border-radius: 8px;
    }
}
// --Шапка--

// Промо текст 

.promo-text{
    .promo-text__block-image{
        position: absolute;
        right: 0;
        top: 0;
        height: 800px;
        width: 370px;
        border-bottom-left-radius: 90px;
        background: #F9BDBD;
        z-index: -1;
    }
    .promo-text__block-image::after{
        content: "";
        position: relative;
        left: -190px;
        top: 170px;
        display: flex;
        width: 568px;
        height: 577px;
        background-image: url(../images/promo-text__stolik.png);
    }
    .promo-text__block-image::before{
        content: "";
        position: absolute;
        left: -264px;
        top: 405px;
        display: flex;
        width: 390px;
        height: 390px;
        background-image: url(../images/listik.png);
    }
    .promo-text__treatment-to-the-client{
        font-family: Nunito;
        font-style: normal;
        font-weight: normal;
        font-size: 36px;
        line-height: 49px;
    }
    .promo-text__treatment-to-the-client span{
        font-weight: bold;
    }
    .promo-text__title{
        font-family: Nunito;
        font-style: normal;
        font-weight: bold;
        font-size: 72px;
        line-height: 98px;
        color: #313235;
    }
    .promo-text__button{
        background: #DAA520;
        border-radius: 4px;
        padding: 14px 24px;
        border: none;
    }
}

// --Промо текст--